import {
  UPDATE_LOADING_STATUS
} from '../mutation-types'

// 
const state = {
  isLoading: false
}
//  mutations
const mutations = {
  [UPDATE_LOADING_STATUS] (state, payload) {
    state.isLoading = payload.isLoading
  }
}

export default {
  state,
  mutations
}



